.main-header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  height: 56px;
}
.main-header__logo {
  position: absolute;
  z-index: 1;
  align-self: baseline;
}
.main-nav {
  width: 100%;
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
}
.main-nav__list {
  background: #81b3d2;
  width: 100%;
  list-style-type: none;
  margin: 0;
  padding: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.main-nav__item {
  box-sizing: border-box;
  width: 100%;
  padding: 17px 10px 12px 10px;
}
.main-nav__link {
  width: 100%;
  display: block;
  box-sizing: border-box;
  box-shadow: 0 1px 2px rgba(1, 1, 1, 0.2);
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  padding: 17px 10px 12px 10px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
}
.main-nav__link:hover,
.main-nav__link:focus {
  background: #669ec0;
  outline: none;
}
.main-nav__link:active {
  background: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.main-nav--opened .main-nav__list {
  display: flex;
  flex-direction: column;
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--opened::after {
  content: "";
  position: absolute;
  bottom: 0;
  z-index: 3;
  height: 7px;
  width: 100%;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
.main-nav--closed .main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 23px;
  height: 22px;
  cursor: pointer;
  border: none;
  background-color: #81b3d2;
  padding: 0;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 4px;
  right: 0;
  width: 23px;
  height: 5px;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  bottom: 4px;
  right: 0;
  width: 23px;
  height: 5px;
  background-color: #ffffff;
}
.main-nav--closed .main-nav__toggle:hover {
  background-color: rgba(129, 179, 210, 0.6);
}
.main-nav--closed .main-nav__toggle:active {
  background-color: rgba(129, 179, 210, 0.3);
}
.main-nav--opened .main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 4;
  display: block;
  width: 22px;
  height: 22px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 22px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (min-width: 768px) {
  .main-nav {
    position: relative;
    width: 100%;
    background-color: #ffffff;
    z-index: 0;
  }
  .main-nav--opened::after {
    display: none;
  }
  .main-nav--closed .main-nav__list,
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    border: none;
    background: #ffffff;
    box-shadow: none;
  }
  .main-nav--closed .main-nav__list::after,
  .main-nav__list::after {
    content: "";
    grid-column: 3/4;
    grid-row: 1/3;
  }
  .main-nav--closed .main-nav__toggle,
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
  .main-nav__link {
    min-width: 153px;
    margin: 0;
    padding: 0;
    box-shadow: none;
    color: #000000;
    font-size: 14px;
    line-height: 21px;
  }
  .main-nav__link:hover,
  .main-nav__link:focus {
    color: #81b3d2;
    background: none;
    outline: none;
  }
  .main-nav__link:active {
    color: #000000;
    background: none;
    opacity: 30%;
  }
  .main-nav__link--active {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    min-width: 480px;
  }
  .main-nav__link {
    padding: 0;
    min-width: 240px;
  }
}
